import axios from "@/plugins/axios";

import endpoints from "@/data/endpoints";

import router from "@/router";

export default {
    async getGoogleRedirect({ commit }) {
        let url = `${endpoints.auth.google}/?redirect_uri=${process.env.VUE_APP_API_URL}/rest/v1/redirect/`;

        let data = await axios.get(url);

        commit("setGoogleRedirect", data);
    },

    async googleLogin({ commit }, { code, state }) {
        if (!state || state == "") return;

        try {
            let data = await axios.post(`${endpoints.auth.google}/?code=${code}&state=${state}`);

            commit("setGoogleAuth", data);
        } catch (error) {
            console.log(error);
        }
    },

    async getMe({ commit }) {
        try {
            let data = await axios.get(`${endpoints.auth.me}`);

            commit("setMe", data);
        } catch (error) {
            commit("setMe", {});

            if (router.currentRoute.name != "login") {
                router.push("login");
            }
        }
    },

    getEvent({ commit, state }, id) {
        let event = state.events.all.find((event) => event.id == id);

        commit("setEvent", event);
    },
}