import Vue from "vue";
import App from "@/App.vue";
import router from "@/router";
import store from "@/store";

import "@/assets/scss/main.scss";

import { BootstrapVue, IconsPlugin } from "bootstrap-vue";

Vue.use(BootstrapVue);

Vue.use(IconsPlugin);

import i18n from "@/plugins/i18n";

store.commit("setLocale", i18n.locale);

import moment from "@/plugins/moment";

Vue.prototype.$moment = moment;

import "@/filters";

import "@/directives";

// apply global mixin
import globalMixin from "@/mixins/global.mixin";
import Cookies from "js-cookie";

Vue.mixin(globalMixin);

// middleware
router.beforeEach(async (to, from, next) => {
	let isLoggedIn = store.state.auth.isLoggedIn;

	let token = Cookies.get("token");

	isLoggedIn = isLoggedIn || !!token;

	let isLayoutAuth = to.matched.some((record) => record.meta.layout === "auth");

	let isRequiresAuth = to.matched.some((record) => record.meta.requiresAuth)

	if (isRequiresAuth) {
		if (isLoggedIn) {
			if (isLayoutAuth) return next({ name: "home" });
		} else {
			if (isRequiresAuth) return next({ name: "login" });
		}
	}

	if (isLayoutAuth) {
		if (isLoggedIn) return next({ name: "home" });
	}

	next();
});

Vue.config.productionTip = false;

new Vue({
	router,
	store,
	i18n,
	render: (h) => h(App),
}).$mount("#app");

store.dispatch("getMe");