import Vue from "vue";

Vue.directive("vuelidate", (el, binding, options) => {
	let $v = options.context.$v,
		field = binding.expression;

	let noDirty = binding.modifiers.noDirty;

	field.split(".").forEach((f) => {
		$v = $v && $v[f];
	});

	if ($v && (noDirty || $v.$dirty) && $v.$invalid) {
		el.setAttribute("vuelidate-invalid", "true");
		el.classList.add("is_invalid");
	} else {
		el.removeAttribute("vuelidate-invalid");
		el.classList.remove("is_invalid");
	}
});

Vue.directive("visible", (el, binding) => {
	let target = binding.expression;

	el.addEventListener("mouseenter", () => {
		let targetElement = document.querySelector(`[data-visible=${target}]`);

		if (!targetElement) throw new Error(`No target found for ${binding.expression}`);

		targetElement.style.visibility = "visible";
	});

	el.addEventListener("mouseleave", () => {
		let targetElement = document.querySelector(`[data-visible=${target}]`);

		if (!targetElement) throw new Error(`No data-visible found for ${binding.expression}`);

		targetElement.style.visibility = "hidden";
	});
});
